body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
  color: black;
}

a:hover {
  color: green;
}

.header-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login-container {
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
}

.login-container img {
  width: 60px;
  height: 60px;
}

.login-container p {
  margin: 6px 0;
}

.login-container button {
  cursor: pointer;
  border: solid silver;
}

table {
  border-collapse: collapse;
  margin: auto;
  width: 90%;
  text-align: center;
}

th,tr,td {
  border: solid silver;
}

tr:nth-of-type(odd) { 
  background: rgba(238,238,238,0.6); 
}

.message_tip {
  text-align: center;
  color: rgba(170,170,170,1);
}
.mentor-github {
  padding-left: 6px;
  text-align: left;
  height: 40px;
}
.mentor-github span{
  color: green;
}
.student-github {
  text-align: left;
  padding-left: 6px;
  font-weight: 500;
}
.Checked {
  background: rgba(130, 216, 130, 0.6);
}
.In-Progress {
  background: rgba(252, 252, 167, 0.6);
}
.Checking {
  background: rgba(255, 51, 102 , 0.6);
}
.ToDo {
  background: rgba(170, 170, 170, 0.6);
}
.NotDone {
  background: rgba(255, 0, 0, 0.6);
}

.table-legend {
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: left;
  margin-top: 20px;
  max-width: 250px;
  border: solid silver;
  padding: 10px;
}
.table-legend-unit:before {
  display: inline-block;
  margin-right: 10px;
  content: '';
  width: 30px;
  height: 10px;
}

.table-legend-unit:nth-of-type(1):before { background: rgba(130, 216, 130, 0.6); }
.table-legend-unit:nth-of-type(2):before { background: rgba(252, 252, 167, 0.6); }
.table-legend-unit:nth-of-type(3):before { background: rgba(255, 51, 102 , 0.6); }
.table-legend-unit:nth-of-type(4):before { background: rgba(170, 170, 170, 0.6); }
.table-legend-unit:nth-of-type(5):before { background: rgba(255, 0, 0, 0.6); }

@media 
only screen and (max-width: 760px),
(min-device-width: 768px) and (max-device-width: 1024px)  {

	table, thead, tbody, th, td, tr { 
    display: block; 
	}
	
	thead tr { 
		position: absolute;
		top: -9999px;
		left: -9999px;
	}
	
	tr { 
    border: 1px solid #ccc;
  }
	
	td { 
		border: none;
		border-bottom: 1px solid #eee; 
		position: relative;
    padding-left: 50%;
    height: 30px;
  }
  .student-github {
    text-align: inherit;
    padding-left: 50%
  }
	
	td:before { 
		position: absolute;
		top: 6px;
		left: 6px;
		width: 45%; 
		padding-right: 10px; 
		white-space: nowrap;
  }

  td:nth-of-type(1):before { content: "Student:"; }
	td:nth-of-type(2):before { content: "Code Jam 'CV':"; }
	td:nth-of-type(3):before { content: "Code Jam 'CoreJS':"; }
	td:nth-of-type(4):before { content: "Code Jam 'DOM, DOM Events':"; }
	td:nth-of-type(5):before { content: "Markup #1:"; }
	td:nth-of-type(6):before { content: "RS Activist:"; }
	td:nth-of-type(7):before { content: "YouTube:"; }
	td:nth-of-type(8):before { content: "Code Jam 'Scoreboard':"; }
	td:nth-of-type(9):before { content: "Game:"; }
	td:nth-of-type(10):before { content: "Presentation:"; }
	td:nth-of-type(11):before { content: "Course work:"; }
}